@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Noto+Serif:ital,wght@0,400;0,700;1,400;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.flex-grow {
  flex-grow: 1;
  display: flex;
}

@media (min-width: 1920px) {
  .buller-logo {
    width: 220px;
  }
}

@media (max-width: 600px) {
  .buller-logo {
    width: 150px;
  }
}

.right-navbar {
  display: flex;
  justify-content: space-around;
  width: 275px;
}

@media (max-width: 420px) {
  .right-navbar {
    width: 200px;
  }
}

.MuiToolbar-gutters {
  padding-right: 0;
}

.navbar-bottom-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.text-box-container {
  position: absolute;
  bottom: 48px;
  left: 0px;
  width: 680px;
  height: 336px;
  background-color: #ffffff;
  color: #000000;
  font-family: "Noto Serif";
  font-size: 36px;
}

@media (min-width: 1920px) {
  .text-box-container {
    left: 193px;
  }
}

@media (max-width: 680px) {
  .text-box-container {
    width: 100%;
  }
}

@media (max-width: 600px) {
  .text-box-container {
    position: static;
    height: 330px;
    background-color: white;
    right: 300px;
    top: 600px;
    width: 100%;
    z-index: 10;
    font-size: 26px;
  }
}

.inner-text-box-container {
  width: 544px;
  height: 196px;
  margin-left: 40px;
  margin-top: 40px;
  margin-right: 96px;
  margin-bottom: 100px;
}

@media (max-width: 600px) {
  .inner-text-box-container {
    width: 80vw;
    font-size: 28px;
    font-weight: 700;
    margin-left: 30px;
    margin-top: 30px;
  }
}

.inner-text-small-box-container {
  font-family: "Poppins";
  font-size: 18px;
  font-weight: 600;
  color: #827264;
  text-decoration: underline;
  margin-top: 26px;
  padding-bottom: 48px;
}

@media (max-width: 600px) {
  .inner-text-small-box-container {
    margin-top: 35px;
  }
}

.background-image {
  display: flex;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 0;
  background-position-x: 30%;
}

@media (min-width: 1920px) {
  .background-image {
    background-position-x: 90%;
    background-size: cover;
    background-image: url(/Buller-maqueta/static/media/Background-xl.642e47b5.png) !important
  }
}

@media (max-width: 1200px) {
  .background-image {
    background-position-x: 50%;
  }
}

@media (max-width: 600px) {
  .background-image {
    background-position: 100 200;
    background-image: url(/Buller-maqueta/static/media/Background-mobile.5a026ad2.png) !important;
    background-position-x: 20%;
  }
}

.tabs-menu-container {
  position: absolute;
  bottom: 0;
  width: 100%;
}

@media (max-width: 600px) {
  .tabs-menu-container {
    display: none;
  }
}

.about-us-container {
  background-color: #968777;
  display: flex;
  justify-content: space-around;
  padding-bottom: 80px;
}

@media (min-width: 1920px) {
  .about-us-container {
    padding-left: 8%;
    padding-right: 8%;
  }
}

.parrafo {
  margin: 0;
  padding: 0;
  font-family: "Noto Serif";
  font-size: 28px;
  font-weight: 700;
  padding-top: 102px;
  color: #ffffff;
  margin-bottom: 0px;
}

@media (min-width: 1920px) {
  .parrafo {
    font-size: 32px;
  }
}

@media (max-width: 600px) {
  .parrafo {
    padding-top: 0;
    margin-left: 20px;
    margin-right: 20px;
    font-size: 24px;
  }
}

.about-us-container-left {
  width: 45%;
}

@media (max-width: 600px) {
  .about-us-container-left {
    margin-top: 53px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow-x: auto;
  }
}

.about-us-container-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 45%;
}

@media (max-width: 600px) {
  .about-us-container-right {
    display: none;
  }
}

.gallery2 {
  display: block;
  height: auto;
  margin-top: 192px;
  width: 100%;
}

@media (max-width: 600px) {
  .gallery2 {
    display: none;
  }
}

.parrafo-small {
  margin-top: 49px;
  font-size: 19px;
  font-family: "Poppins";
  font-weight: 400;
  color: white;
  margin-bottom: 0px;
}

@media (min-width: 1920px) {
  .parrafo-small {
    font-size: 22px;
  }
}

@media (max-width: 600px) {
  .parrafo-small {
    margin-top: 15px;
    margin-left: 20px;
    margin-right: 20px;
    font-size: 16px;
  }
}

.gallery1 {
  margin-top: 82px;
  display: flex;
  width: 100%;
  margin-bottom: 0px;
}

@media (max-width: 600px) {
  .gallery1 {
    display: none;
  }
}

.gallery3 {
  display: flex;
  margin-top: 40px;
  display: flex;
  width: 100%;
}

@media (max-width: 600px) {
  .gallery3 {
    display: none;
  }
}

.footer-container {
  display: flex;
  height: 70px;
  background-color: #574332;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
}

@media (max-width: 600px) {
  .footer-container {
    height: 103px;
  }
}

.footer-text {
  display: flex;
  padding: 0px;
  margin: 0px;
  color: white;
  font-family: "Poppins";
  font-size: 16px;
  text-align: center;
}

@media (min-width: 1920px) {
  .footer-text {
    font-size: 22px;
  }
}

@media (max-width: 600px) {
  .footer-text {
    font-size: 12px;
    margin: 20px 48px 20px 48px;
    text-align: center;
    font-weight: 400;
  }
}

.arrow-container {
  display: flex;
  position: absolute;
  top: calc(100vh - 60px);
}

@media (min-width: 600px) {
  .arrow-container {
    display: none;
  }
}

.arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 74px;
  height: 60px;
  background-color: #191919;
}

.Carousel {
  padding-left: 5px;
  padding-top: 29px;
  padding-bottom: 34px;
  overflow-x: scroll;
  max-width: 100vw;
}

@media (min-width: 600px) {
  .Carousel {
    display: none;
    overflow-x: hidden;
  }
}

.Carousel-inner {
  display: flex;
  flex-direction: row;
  width: auto;
}

.Carousel-inner-inner {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

@media (min-width: 600px) {
  .carousel-div-container {
    display: none;
  }
}

.Carousel-image {
  margin-left: 5px;
  margin-right: 5px;
  width: 300px;
  height: 300px;
  object-fit: cover;
}

